.about{
    text-align: center;
    display: inline-block;
}

.container{
    text-align: center;
}

.typing{
    color: white;
    white-space: nowrap;
    margin: 0 auto;
    overflow: hidden;
    border-right: .50em solid #26FFA7;
    font-family: monospace;
    font-size: 2em;
    width: 100%;
    animation: 
        typing 2s steps(20, end),
        2.5s blink-caret .5s step-end infinite;
     
}   

@keyframes typing {
    from { width: 0 }
}
  
      
/* The typewriter cursor effect */
@keyframes blink-caret {
    50% {border-color: transparent}
}